@charset "UTF-8";
.global-site .menu-links__container {
  padding: 10px 0;
  text-align: center; }
  @media screen and (min-width: 320px) {
    .global-site .menu-links__container {
      text-align: none; } }

.global-site .nav__item--menu, .global-site ul li a {
  height: 100%;
  text-decoration: underline;
  padding: 5px;
  font-weight: 700;
  color: #fff;
  font-size: 16px;
  display: inline-block; }
  @media screen and (min-width: 320px) {
    .global-site .nav__item--menu, .global-site ul li a {
      padding: 0 2px;
      font-size: 12px;
      text-transform: uppercase;
      text-decoration: none; } }

.global-site__body {
  padding: 0 10px; }
  .global-site__body .heading--global {
    padding: 10px 0 0;
    font-size: 16px;
    margin: 0 0 5px;
    text-align: center; }
    .global-site__body .heading--global-description {
      font-size: 25px;
      padding: 30px 0 15px;
      text-transform: inherit; }
  .global-site__body .outer-list {
    display: block; }
    .global-site__body .outer-list__item {
      display: block;
      padding: 15px 0; }
    .global-site__body .outer-list__title {
      font-weight: bold;
      text-align: center;
      padding: 10px 0; }
  .global-site__body .inner-list {
    display: block; }
    .global-site__body .inner-list__item {
      display: block;
      padding: 5px;
      border-bottom: 1px solid #b0b9bb; }
      .global-site__body .inner-list__item:after {
        display: inline-block;
        float: right; }
        @media only screen and (min-width: 260px) {
          .global-site__body .inner-list__item:after {
            content: ">"; } }
        @media only screen and (min-width: 320px) {
          .global-site__body .inner-list__item:after {
            content: "❯"; } }
    .global-site__body .inner-list__link {
      color: #000; }
      @media only screen and (min-width: 320px) {
        .global-site__body .inner-list__link-icon {
          display: inline-block;
          padding: 0 5px 0 0; } }

.global-site__footer-logo {
  text-align: center;
  display: block;
  padding: 0 0 10px; }
  .global-site__footer-logo .logo {
    max-height: 40px; }

.global-site__footer-menu {
  text-align: center;
  padding: 20px 0; }

.global-site ul {
  display: inline-block; }
  .global-site ul li {
    list-style: none;
    display: inline-block; }
